    padding: 0px 20px;
    height: 60px;
    background-color: rgb(255, 230, 0);
    margin: 2px 0px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -webkit-box-shadow: 1px 1px 2px rgb(184, 182, 182);
            box-shadow: 1px 1px 2px rgb(184, 182, 182);
}
.Format div{
    text-align: left;
}
.Format div label.format-type{
    color:rgb(83, 83, 83);
    font-weight: 600;
}
.Format div label.format-ext{
     color:rgb(83, 83, 83);
