.App {
  text-align: center;
  margin-top: 30px
}
div.formats{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  -ms-flex-flow: column;
      flex-flow: column;
}
.App form{
  width: 99%;
  padding: 4px 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.App form input[type='text']{
  font-size: 15px;
  width: 500px;
  border: 2px solid rgb(176, 0, 199);
  border-radius: 50px;
  padding: 5px;
  margin-right: 5px;
}
.App form input[type='submit']{
  background-color: rgb(176, 0, 199);
  font-size: 20px;
  font-weight: 600;
  border-radius: 200px;
  outline-color: white;
  outline-width: 3px;
  color: white;
  cursor: pointer;
  border-radius: 10px;
  padding: 10px;
  margin-right: 5px;
}
.App form input[type='text']:focus{
  -webkit-box-shadow: 1px 1px 3px  rgb(255, 68, 255);
          box-shadow: 1px 1px 3px  rgb(255, 68, 255);
}



